#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}.skillsTitle{
    font-size: 3rem;
    font-weight: 500;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}.skillsDesc{
    font-weight:200 ;
    font-size: 0.9rem;
    max-width: 50rem;
    padding: 0 2rem;
}.skillsBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
    color: white;
}.skillsBar{
    display: flex;
    margin: 1rem;
    padding: 1.5rem;
    border-radius: 1.5rem;
    background: grey;

}.skillsBarImg{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
}.skillsBarText>p{
    font-size: 0.9rem;
    font-weight:200 ;
}@media screen and (max-width:720px){
    .skillsBarText>p{
        font-size: 2vw;
    }
}
@media screen and (max-width:350px){
    .skillsBarText>p{
        font-size: 2vw;
    }.skillsBarImg{
        height: 2.25rem;
        width: 2.25rem;
    }.skillsBarText>h2{
        font-size: 5vw;
    }
}