.navbar{
    background-color: rgb(131, 126, 126);
    height: 5rem;
    width: 100vw;
    max-width: 75rem;
    margin:0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3  ;
}.logo{
    object-fit: cover;
    height: 4rem;
    width: 4rem;
    border-radius: 20px;
}.desktopMenuListItem{
    margin: 1rem;
    cursor: pointer;
}
.desktopMenuListItem:hover{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom:3px solid;
}.desktopMenuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;

}.desktopMenuBtn{
 background-color: white;
 border: none;
 color:black;
 display: flex;
 justify-content: center;
 align-items: center;
 padding: 0 1rem;
 border-radius: 2rem;

}@media screen and (max-width:720px){
    .desktopMenuListItem{
        font-size:2vw;
        
    }
}
@media screen and (max-width:400px){
    .desktopMenuListItem{
        font-size:2vw;

        
    }
}.active{
    color: yellow;
    border-bottom: 3px solid yellow;
}
.MobMenu{
    display: none;
    object-fit: cover;
    height: 1.8rem;
}.navMenu{
    position: absolute;
    top:4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem ;
    height: fit-content;
    min-width: 15rem;
    background-color:rgb(172, 165, 165);
    border-radius: 1rem;

}.lisItem{
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(112, 104, 104);
    border-radius: 1rem;
}@media screen and (max-width:720px){
    .MobMenu{
        display: flex;
    }.desktopMenu{
        display: none;
    }.desktopMenuBtn{
        display: none;
    }
}