#contactPage{
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    margin:0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}.contactPageTitle{
    font-size: 3rem;
    margin-bottom: 1rem;
}.clientDesc{
    font-weight: 300;
    font-size: 1rem;
}.clientsImgs{
    margin: 1rem 0;
    display: flex;
    flex-wrap: wrap;


}.client{
    object-fit: cover;
    width: 23%;
    min-width: 9rem;
    padding: 0.25rem;
    margin: auto;
}
#clients{
    padding: 2rem;
}.contactDesc{
    font-size: medium;
    font-weight: 300;
}.contactForm{
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
}.name, .email, .Msg{
    font-size: medium;
    width:100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color:white;
    border-radius: 1rem;
    background:grey;
    border: none;
}.submitBtn{
    border: none;
    background: #fcfafa;
    border-radius: 1rem;
    margin: 2rem;
    padding: 0.5rem 2.5rem;
    font-size: 1rem;
    color: black;
}.links{
    display: flex;
    flex-wrap: wrap;
}.link{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75rem;
}