#works{
    margin: 0 auto;
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
}.worksTitle{
    font-size: 3rem;
    margin-bottom: 1.5rem 0;
}.worksDesc{
    font-weight:300 ;
    font-size: 0.9rem;
    text-align: center;
    max-width: 45rem;
    
}.worksImgs{
    display:flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100vw;
    max-width: 65rem;
}.worksImg{
    object-fit: cover;
    height: 20rem;
    margin: 0.5rem;
}.worksBtn{
    border: none;
    background: #fcfafa;
    border-radius: 1rem;
    margin: 3rem 0;
    padding: 0.5rem 2.5rem;
    align-items: center;
    font-size: 1rem;
}@media screen and (max-width:585px){
    .worksImg{
        height: 50vh;
    }
}