#intro{
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}.bg{
    position: absolute;
    top: -4rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 100vh;
    background: transparent;
    border-radius: 10px;
}.introContent{
    height: 100vh;
    width:100vw;
    padding: 2rem;
    font-size: 2rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}.hello{
    font-size: 1.75rem;
    font-weight: 100;
}.introName{
    color: yellow;

}.introPara{
    font-size: medium;
    letter-spacing: 1px;
    font-weight: 200;
}.btn{
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 2rem;
    border-radius: 1rem;
    border: none;
    align-items: center;
}.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 2rem;}
@media screen and (max-width:720px) {
    .bg{
        right: -10vw;
    }.introContent{
        font-size: 10vw;
    }.hello{
        font-size: medium;
    }
}
@media screen and (max-width:450px) {
    .bg{
        left: -10vw;
    }.introContent{
        color: white;
    }
}

